<template>
  <div>
    <v-container>

      <!--  -->
      <p class="ex_message">{{ message }}</p>

      <!--  -->
      <v-row>
        <v-col>
          <v-text-field 
            v-model="user.name"
            label=""
            :rules="rules"
            prepend-icon="mdi-account-arrow-right"
          ></v-text-field>
        </v-col>
      </v-row>

      <!--  -->
      <v-row>
        <v-col>
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
          >
            <v-text-field
              v-model="user.email"
              :rules="emailRules"
              label=""
              prepend-icon="mdi-email"
            ></v-text-field>
          </v-form>
        </v-col>
      </v-row>

      <!--  -->
      <v-row>
        <v-col>
          <v-form ref="form"
            v-model="valid"
            lazy-validation
          >
            <v-text-field
              v-model="oldPassword"
              :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
              :rules="passwordRules"
              :type="show ? 'text' : 'password'"
              label=""
              hint="8"
              counter
              prepend-icon="mdi-shield-key"
              @click:append="show = !show"
            ></v-text-field>
          </v-form>
        </v-col>
      </v-row>
      <!--  -->
      <v-row>
        <v-col>
          <v-form ref="form"
            v-model="valid"
            lazy-validation
          >
            <v-text-field
              v-model="newPassword"
              :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
              :rules="passwordRules"
              :type="show ? 'text' : 'password'"
              label=""
              hint="8"
              counter
              prepend-icon="mdi-shield-key"
              @click:append="show = !show"
            ></v-text-field>
          </v-form>
        </v-col>
      </v-row>

      <!--  -->
      <v-row>
        <v-col>
          <v-form ref="form"
            v-model="valid"
            lazy-validation
          >
            <v-text-field
              tyep="number"
              :rules="ageRules"
              v-model="user.age"
              label=""
              hint="0~100"
              prepend-icon="mdi-chart-donut"
            ></v-text-field>
          </v-form>
        </v-col>
        <v-col>
          <v-form 
            ref="form"
            v-model="valid"
            lazy-validation
          >
            <v-select
              :items="sexData"
              label=""
              v-model="user.sex"
              prepend-icon="mdi-account-group"
            ></v-select>
          </v-form>
        </v-col>
      </v-row>

      <!--  -->
      <v-btn 
        color="primary"
        @click="createUser"
      ><v-icon left>mdi-account-plus</v-icon></v-btn>

    </v-container>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      user: [],
      name: "", 
      email: "",
      oldPassword: "",
      newPassword: "",
      age:  "",
      sexData: ["","",""],
      sex: "",
      show: false,
      valid: true,
      message: "",
      emailRules: [
        value => !!value || '',
        value => {
          const pattern = /^(([^<>()[\]\\.,;:\s@]+(\.[^<>()[\]\\.,;:\s@]+)*)|(.+))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
          return pattern.test(value) || ''
        },
      ],
      passwordRules: [
        value => !!value || '',
        value => value.length >= 8 || '8',
      ],
      ageRules: [
        value => !!value || '',
        value => {
          const number = /^[1-9]|[1-9][0-9]$/
          return number.test(value) || '0~99'
        }
      ],
      rules: [
        value => !!value || '',
      ],
    }
  },
  mounted() {
    axios.get('http://localhost:3000/api/v1/users-show',{
      params: {user_id: this.$store.state.id}
    })
    .then(res => {
      this.user = res.data.user
      console.log(res.data.user)
    })
  },
  methods: {
    createUser() {
      axios.post(
        'http://localhost:3000/api/v1/user-update',
        {
          id: this.$store.state.id,
          name: this.user.name,
          email: this.user.email,
          oldPassword: this.oldPassword,
          newPassword: this.newPassword,
          age: this.user.age,
          sex: this.user.sex
        })
      .then((res) => {
        this.message = res.data.message
        console.log(res.data.status)
        if (res.data.status == 'success') {
          this.$store.state.value = true
          this.$store.state.flashType = "success"
          this.$store.state.message = ""
          this.$router.push('/home')
        }
      })
      .catch( () => {
          this.message = ""
        })
    }
  }
}
</script>